import useSWR from "swr";
import { GetURL } from "../utils";
import fetch from "unfetch";

const fetcher = (url) => fetch(GetURL("features")).then((r) => r.json());

export default function features() {
  const { data, error } = useSWR(GetURL("features"), fetcher);

  if (error) return <div>failed to load</div>;
  if (!data) return <div>loading...</div>;

  console.log(data);

  return (
    <>
      <section id="service" className="margin-b50">
        <div className="container">
          <div className="row">
            <div className="col-md-12">
              <div className="heading text-center">
                <h4>What we Feaures?</h4>
                <h2>Our Features</h2>
              </div>
            </div>
          </div>
        </div>
      </section>
      <section id="service_inner" className="my_services">
        <div className="container">
          <div className="row">
            {data ? (
              data.map((item) => (
                <div key={item.id} className="col-md-3 col-sm-6">
                  <div className="service_inner">
                    <div className="interest_icon">
                      <img
                        src={`${item.image.url}`}
                        className="img-responsive service_img"
                        alt=""
                      />
                      <img
                        src={`${item.hoverImg.url}`}
                        className="img-responsive service_img_hover"
                        alt=""
                      />
                    </div>
                    <h4>{item.title}</h4>
                    <p> {item.description} </p>
                  </div>
                </div>
              ))
            ) : (
              <div />
            )}
          </div>
        </div>
      </section>
    </>
  );
}
